import { request } from 'utils';

request.prefix = '/api/categories';

export const getListPrizeInTrainType = ({ page, pageSize = 10, isPagingEnabled = true }) =>
  request({
    url: '/PrizeInTrainType',
    params: {
      page,
      pageSize,
      isPagingEnabled,
    },
    method: 'GET',
  });

export const getPrizeInTrainType = (id, code) =>
  request({
    url: `/PrizeInTrainType/GetById`,
    method: 'GET',
    params: { id, code },
  });

export const deletePrizeInTrainType = record =>
  request({
    url: `/PrizeInTrainType`,
    method: 'DELETE',
    data: { ...record },
  });

export const updatePrizeInTrainType = ({ prizeClassId, traintypeid, ...values }) =>
  request({
    url: `/PrizeInTrainType`,
    data: {
      prizeClassId,
      traintypeid,
      amount: values.amount,
    },
    method: 'PUT',
  });

export const createPrizeInTrainType = values =>
  request({
    url: '/PrizeInTrainType',
    data: {
      trainTypeId: values.trainTypeId,
      prizeClassId: values.prizeClassId,
      amount: values.amount,
    },
    method: 'POST',
  });

export const listPrizeClassification = () =>
  request({
    url: '/PrizeClassification',
    params: {
      isPagingEnabled: false,
    },
    method: 'GET',
  });



