import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import InputMoney from 'components/common/input/InputMoney';
import { Row, Card, Form, message, Select } from 'antd';
import {
  getPrizeInTrainType,
  updatePrizeInTrainType,
  createPrizeInTrainType,
  listPrizeClassification,
} from 'services/PrizeInTrainType';
import { getListTrainingSystem } from 'services/TrainingSystem';

const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    sys: '',
    grad: '',
    gradCode: '',
    fromScores10: '',
    fromScores4: '',
    fromTrainingScore: '',
    amountOfMoney: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formStipulate' })
class FormStipulateAmountOfScholarship extends Component {
  state = {
    prizeClassId: false,
    // eslint-disable-next-line react/no-unused-state
    traintypeid: '',
    initialValue: {},
    listTraningSytems: [],
    listPrizeClassifications: [],
  };

  componentDidMount = () => {
    const { match } = this.props;
    this.getDataSelect();
    if (match.params.id) {
      // eslint-disable-next-line react/no-unused-state
      this.setState({ prizeClassId: match.params.id, traintypeid: match.params.code });
      this.getDataById(match.params.id, match.params.code);
    }
  };

  getDataById = async (id, code) => {
    const res = await getPrizeInTrainType(id, code);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  getDataSelect = () => {
    getListTrainingSystem(1, false)
      .then(res => {
        this.setState({
          listTraningSytems: res.data.items,
        });
      })
      .catch(error => {
        console.log('====>', error);
      });
    listPrizeClassification().then(res => {
      this.setState({
        listPrizeClassifications: res.data.items,
      });
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { prizeClassId, traintypeid } = this.state;
    try {
      if (!prizeClassId) {
        await createPrizeInTrainType(values);
        message.success('Tạo thành công');
      } else {
        await updatePrizeInTrainType({ prizeClassId, traintypeid, ...values });
        message.success('Chỉnh sửa thành công');
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { prizeClassId, listTraningSytems, listPrizeClassifications, initialValue } = this.state;
    return (
      <Card
        title={
          prizeClassId ? 'Cập nhật quy định số tiền học bổng' : 'Thêm quy định số tiền học bổng'
        }
      >
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item {...formItemLayout} label="Hệ" hasFeedback>
              {getFieldDecorator('trainTypeId', {
                initialValue: initialValue.trainTypeId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select style={{ width: '100%' }} placeholder="Chọn hệ đào tạo">
                  {listTraningSytems.length > 0
                    ? listTraningSytems.map(item => <Option key={item.id}>{item.label}</Option>)
                    : null}
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Xếp loại" hasFeedback>
              {getFieldDecorator('prizeClassId', {
                initialValue: initialValue.prizeClassId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select style={{ width: '100%' }} placeholder="Chọn xếp loại">
                  {listPrizeClassifications.length > 0
                    ? listPrizeClassifications.map(item => (
                        // eslint-disable-next-line react/jsx-indent
                        <Option key={item.id}>{item.label}</Option>
                      ))
                    : null}
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số tiền" hasFeedback>
              {getFieldDecorator('amount', {
                initialValue: initialValue.amount,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<InputMoney />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormStipulateAmountOfScholarship {...props} />
  </Page>
));



